
html {
  scroll-behavior: smooth;
}

html,
body,
ul,
li,
a,
button,
input,
textarea {
  margin: 0;
  padding: 0;
  font-family: roboto, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 1.5;
  text-decoration: none;
  list-style-type: none;
  color: #fff;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

* {
  box-sizing: border-box;
}
